
import Vue from 'vue'
import Component from 'vue-class-component'
import GlovoRadioGroup from 'common/ui/GlovoRadioGroup.vue'
import GlovoSearchField from 'common/ui/GlovoSearchField.vue'
import { Prop } from 'vue-property-decorator'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { Getter } from 'vuex-class'
import CollectionSelector from '@/components/common/ui/CollectionSelector.vue'
import {
  ProductStatusFilter,
  ProductStatusFilterTranslationKey,
  PRODUCTS_FILTERS_INITIAL_STATE
} from '@/modules/stock/types/ProductFilter'
import { MenuItemType } from '@/components/common/ui/collection-list-types'

import type { FeatureToggle } from '@/modules/feature-toggles/store/state'
import type { ProductFilters } from '@/modules/stock/types/ProductFilter'
import type { StoreAddressId } from '@/modules/store/data/resources'

@Component({
  name: 'ProductsFilter',
  components: {
    GlovoSearchField,
    CollectionSelector,
    GlovoRadioGroup
  }
})
export default class ProductsFilter extends Vue {
  @Prop({ default: null }) storeAddressId!: StoreAddressId
  @Prop({ default: PRODUCTS_FILTERS_INITIAL_STATE }) filterBy!: ProductFilters

  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>

  get listStatus() {
    return Object.entries(ProductStatusFilter).map(([, value]) => ({
      value,
      label: this.$t(ProductStatusFilterTranslationKey[value])
    }))
  }

  get isProductSearchEnabled() {
    return this.toggles.FE_PARTNERS_PRODUCTS_SEARCH_ENABLED
  }

  get searchTerm() {
    return this.filterBy.searchTerm
  }

  set searchTerm(value) {
    this.$emit('update-filter', 'searchTerm', value)
  }

  filterByProductGroup(type: MenuItemType, id: number) {
    this.$emit('update-filter', 'group', { type, id })
  }

  filterByStatus(value: ProductStatusFilter) {
    this.$emit('update-filter', 'status', value)
  }
}
