
import Vue from 'vue'
import Component from 'vue-class-component'
import { flattenDeep } from 'lodash'
import ToggleBanner from '../ToggleBanner/ToggleBanner.vue'
import ProductsFilters from './ProductsFilters.vue'
import ProductItem from './ProductItem.vue'
import ProductsList from './ProductsList.vue'
import ProductsListOld from './ProductsListOld.vue'
import Placeholder from '@/components/common/Placeholder.vue'
import Stockout from '@/components/main/popups/Stockout.vue'
import EmptyCollection from '../EmptyCollection.vue'
import { Prop } from 'vue-property-decorator'
import {
  Product,
  ProductId,
  StoreAddressId,
  StoreCollection,
  StoreCollectionGroup,
  StoreCollectionGroupId,
  StoreCollectionId,
  StoreCollectionSection,
  StoreCollectionSectionId,
  StoreCollectionSectionVM
} from '@/modules/store/data/resources'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'
import { Getter } from 'vuex-class'
import {
  GET_PRODUCTS_GETTER,
  GET_STORE_COLLECTION_GROUPS_GETTER,
  GET_STORE_COLLECTION_SECTIONS_GETTER,
  GET_STORE_COLLECTIONS_GETTER
} from '@/modules/store/data/getters'
import { GET_IS_SEARCHING_GETTER } from '@/modules/core/store/getters'
import { core } from '@/modules/core/store/namespace'
import { stockManagement } from '@/modules/stock/store/namespace'
import {
  REMOVE_PRODUCT_STOCKOUT_NEW,
  RemoveProductStockoutNewPayload,
  SET_IS_PRODUCT_SELECTED,
  SetIsProductSelectedPayload,
  UNDO_PRODUCTS_STOCKOUT,
  UndoProductsStockoutPayload
} from '@/modules/stock/store/action-types'
import { StoreAction } from '@/modules/store/types/store'
import { NonEmptyArrayOf } from 'types/NonEmptyArrayOf'
import {
  DISPLAY_STOCK_PRODUCTS,
  DisplayType
} from '@/modules/store/services/display-types'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import { authentication } from '@/modules/authentication/store/namespace'
import { StoreAdmin } from '@/modules/store/data/resources'
import mParticle from '@/modules/mparticle'
import { BusinessEventNames } from 'models/BusinessEvents'
import {
  ProductFilters,
  ProductStatusFilter,
  PRODUCTS_FILTERS_INITIAL_STATE
} from '@/modules/stock/types/ProductFilter'
import { MenuItemType } from '@/components/common/ui/collection-list-types'

export interface Section {
  products: Product[]
  name: string
  id: number
}

export interface Collection {
  sections: Section[]
  name: string
  id: number
  storeAddressId: number
}

export interface CollectionGroup {
  collections: Collection[]
  title?: string
  id?: number
  storeAddressId?: number
}

@Component({
  name: 'Products',
  components: {
    ToggleBanner,
    Placeholder,
    Stockout,
    ProductsFilters,
    ProductItem,
    ProductsList,
    ProductsListOld,
    EmptyCollection
  }
})
export default class Products extends Vue {
  @Prop({ default: null }) storeAddressId!: StoreAddressId
  @Prop({ default: null }) activeStoreAddressId!: StoreAddressId

  loading = false
  isStockoutProductsModalDisplayed = false
  selectedProducts: Product[] | null = null
  filterBy: ProductFilters = PRODUCTS_FILTERS_INITIAL_STATE

  @Getter display!: DisplayType // TODO to be migrated after all modules
  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>

  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  storeAdmin?: StoreAdmin

  @Getter(GET_STORE_COLLECTION_GROUPS_GETTER) storeCollectionGroups!: Record<
    StoreCollectionGroupId,
    StoreCollectionGroup
  >

  @Getter(GET_STORE_COLLECTIONS_GETTER) storeCollections!: Record<
    StoreCollectionId,
    StoreCollection
  >

  @Getter(GET_STORE_COLLECTION_SECTIONS_GETTER)
  storeCollectionSections!: Record<
    StoreCollectionSectionId,
    StoreCollectionSection
  >

  @Getter(GET_PRODUCTS_GETTER)
  storeProducts!: Record<ProductId, Product>

  @core.Getter(GET_IS_SEARCHING_GETTER)
  isSearching!: boolean

  @stockManagement.Action(REMOVE_PRODUCT_STOCKOUT_NEW)
  removeProductStockoutNew!: StoreAction<RemoveProductStockoutNewPayload>

  @stockManagement.Action(SET_IS_PRODUCT_SELECTED)
  setIsProductSelected!: StoreAction<SetIsProductSelectedPayload>

  @stockManagement.Action(UNDO_PRODUCTS_STOCKOUT)
  undoProductsStockout!: StoreAction<UndoProductsStockoutPayload>

  get selectedProductIds() {
    return this.selectedProducts?.map(({ id }) => id)
  }
  get selectedProductNames() {
    return this.selectedProducts?.map(({ name }) => name)
  }

  get collectionGroups(): CollectionGroup[] {
    if (this.isCurrentlyBeingViewed) {
      return Object.keys(this.storeCollectionGroups).map(
        this.mapCollectionGroup
      )
    }
    return []
  }

  get isFilteredByStatusEnabled() {
    return this.filterBy.status === ProductStatusFilter.ENABLED
  }

  get isFilteredByStatusDisabled() {
    return this.filterBy.status === ProductStatusFilter.DISABLED
  }

  get isFilteredByCollection() {
    return this.filterBy.group.type === MenuItemType.COLLECTION
  }

  get isFilteredBySection() {
    return this.filterBy.group.type === MenuItemType.SECTION
  }

  get filteredProductIds() {
    if (this.collectionGroups.length > 0) {
      const productIds = new Set(
        flattenDeep(
          Object.values(this.collectionGroups).map(collectionGroup => {
            let collections = [...collectionGroup.collections]

            if (this.isFilteredByCollection) {
              collections = collectionGroup.collections.filter(collection => {
                return this.filterBy.group.id === collection.id
              })
            }

            return collections.map(collection => {
              let sections = [...collection.sections]

              if (this.isFilteredBySection) {
                sections = collection.sections.filter(
                  section => this.filterBy.group.id === section.id
                )
              }

              return sections.map(section => {
                let products = [...section.products]

                if (this.isSearching) {
                  products = products.filter(product => {
                    const searchRegEx = new RegExp(
                      this.filterBy.searchTerm,
                      'i'
                    )

                    return product.name.match(searchRegEx)
                  })
                }

                if (this.isFilteredByStatusEnabled) {
                  products = products.filter(product => product.enabled)
                } else if (this.isFilteredByStatusDisabled) {
                  products = products.filter(product => !product.enabled)
                }

                return products.map(product => product.id)
              })
            })
          })
        )
      )
      return [...productIds]
    }
    return []
  }

  get filteredProducts() {
    return this.filteredProductIds.map(
      productId => this.storeProducts[+productId]
    )
  }

  get isCurrentlyBeingViewed() {
    return (
      this.storeAddressId === this.activeStoreAddressId &&
      this.display.name === DISPLAY_STOCK_PRODUCTS.name
    )
  }

  get areMenuManagementChangesEnabled() {
    return this.toggles.FE_PARTNERS_MENU_MANAGEMENT_CHANGES_ENABLED
  }

  get totalSelectedProducts() {
    const products: Product[] = []
    this.filteredProducts.forEach(product => {
      if (product?.selected && !products.includes(product))
        products.push(product)
    })
    return products
  }

  mapCollectionGroup(groupId: StoreCollectionGroupId | string | null) {
    if (groupId != null) {
      const collectionGroup = this.storeCollectionGroups[+groupId]
      const collections = collectionGroup.collections.map(collectionId => {
        const collection = this.storeCollections[collectionId]
        const sections = collection.sections.map(sectionId => {
          const section = this.storeCollectionSections[sectionId]
          const products = section.products.map(productId => {
            return this.storeProducts[productId]
          })
          return { ...section, products }
        })
        return { ...collection, sections }
      })
      return { ...collectionGroup, collections }
    }
    return { collections: [] }
  }

  setSelectedProducts(selectedProducts: Product[]) {
    this.selectedProducts = selectedProducts
    this.isStockoutProductsModalDisplayed = true
  }

  closeStockoutProductsModal() {
    this.isStockoutProductsModalDisplayed = false
    if (this.selectedProducts) {
      this.unselectAllProducts(this.selectedProducts)
    }
  }

  cancelStockoutProductsModal() {
    const storeAdminId = this.storeAdmin ? this.storeAdmin.id : 0
    // TS Ignore comment to be taken care of in task - https://glovoapp.atlassian.net/browse/PAR-2254
    // @ts-ignore
    this.$analytics.onProductCancelPressed(this.storeAddressId)

    mParticle.logEvent(
      BusinessEventNames.productDisableCancel,
      mParticle.EventType.UserContent,
      {
        storeAddressId: this.storeAddressId,
        storeAdminId
      }
    )

    this.closeStockoutProductsModal()
  }

  unselectAllProducts(products: Product[]) {
    products.forEach(({ id }) => {
      this.setIsProductSelected({
        id,
        isSelected: false
      })
    })
  }

  getSelectedProductsFromSection(section: StoreCollectionSectionVM) {
    const products = section.products.filter(p => p.selected)
    return products.length > 0 ? products : section.products
  }

  getProductIds(products: Product[]) {
    return products.map(product => product.id)
  }

  nothingSelected(section: StoreCollectionSectionVM) {
    return !section || section.products.filter(p => p.selected).length === 0
  }

  async enableSelectedProducts() {
    this.loading = true
    const products = this.totalSelectedProducts
    const productIds = this.getProductIds(products)
    await this.removeProductStockoutNew({
      storeAddressId: this.storeAddressId,
      productIds: productIds as NonEmptyArrayOf<ProductId>
    })
    this.loading = false
    this.showUndoBanner(productIds)
    this.unselectAllProducts(products)
  }

  showUndoBanner(productIds: ProductId[]) {
    this.$toast.show({
      message: this.$tc('stockout.products.enabled', productIds.length),
      actionLabel: this.$t('common.undo').toString(),
      onActionClick: () => this.undoProductsStockout(this.storeAddressId)
    })
  }

  disableSelectedProducts() {
    this.selectedProducts = this.totalSelectedProducts
    this.isStockoutProductsModalDisplayed = true
  }

  cancelSelectedProducts() {
    this.totalSelectedProducts.forEach(p => {
      if (p.selected)
        this.setIsProductSelected({
          id: p.id,
          isSelected: false
        })
    })
  }

  onUpdateFilter(type: keyof ProductFilters, value: any) {
    this.filterBy[type] = value
  }
}
