
import Icon from 'common/Icon.vue'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import {
  Attribute,
  Product,
  RequestPriceUpdate,
  StoreAddressId,
  StoreAdmin
} from '@/modules/store/data/resources'
import { authentication } from '@/modules/authentication/store/namespace'
import {
  REMOVE_REQUEST_PRICE_UPDATE,
  RemoveRequestPriceUpdatePayload,
  REQUEST_ATTRIBUTE_PRICE_UPDATE,
  REQUEST_PRODUCT_PRICE_UPDATE,
  RequestAttributePriceUpdatePayload,
  RequestProductPriceUpdatePayload,
  RESTORE_REQUEST_PRICE_UPDATE
} from '@/modules/stock/store/action-types'
import { stockManagement } from '@/modules/stock/store/namespace'
import {
  StoreAction,
  StoreActionWithNoPayload
} from '@/modules/store/types/store'
import GlovoBtn from 'common/GlovoBtn.vue'
import GlovoModal from 'common/ui/modal/GlovoModal.vue'
import CurrencyService from '@/services/currency/CurrencyService'
import VueAutonumeric from 'common/VueAutonumeric.vue'

@Component({
  name: 'NewEditPrice',
  components: {
    VueAutonumeric,
    Icon,
    GlovoBtn,
    GlovoModal
  }
})
export default class NewEditPrice extends Vue {
  @Prop() item!: Product | Attribute
  @Prop({
    default: null
  })
  type!: string | null
  @Prop({
    default: null
  })
  value!: RequestPriceUpdate | null
  @Prop({
    default: null
  })
  storeAddressId!: StoreAddressId | null
  @Prop({
    default: null
  })
  currentProductPrice!: string | null
  @Prop({
    default: null
  })
  currentAttributePrice!: number | null

  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER) user?: StoreAdmin

  @stockManagement.Action(REQUEST_PRODUCT_PRICE_UPDATE)
  requestProductPriceUpdate!: StoreAction<RequestProductPriceUpdatePayload>
  @stockManagement.Action(REQUEST_ATTRIBUTE_PRICE_UPDATE)
  requestAttributePriceUpdate!: StoreAction<RequestAttributePriceUpdatePayload>
  @stockManagement.Action(REMOVE_REQUEST_PRICE_UPDATE)
  removeRequestPriceUpdate!: StoreAction<RemoveRequestPriceUpdatePayload>
  @stockManagement.Action(RESTORE_REQUEST_PRICE_UPDATE)
  restoreRequestPriceUpdate!: StoreActionWithNoPayload

  private isOpen = false

  newPrice = this.itemPrice
  isSavingPriceUpdate = false
  isRemovingPriceUpdate = false

  get shouldShowRequestedPriceUpdate() {
    return !!this.value && !this.isRemovingPriceUpdate
  }

  get adminUserId() {
    return this.user ? this.user.id : null
  }

  get itemPrice() {
    return this.currentProductPrice || this.currentAttributePrice
  }

  get itemPriceFormatted() {
    return CurrencyService.format(this.itemPrice!)
  }

  get currencySymbol() {
    return CurrencyService.getCurrencySymbol()
  }

  async saveRequest() {
    if (this.type === 'product') {
      // @ts-ignore
      this.$analytics.onProductPriceSavePressed(
        this.item.id,
        this.storeAddressId,
        this.adminUserId,
        this.currentProductPrice,
        this.newPrice
      )
      return this.requestProductPriceUpdate({
        productId: this.item.id,
        price: Number(this.newPrice),
        storeAddressId: this.storeAddressId!
      })
    }
    if (this.type === 'attribute') {
      // @ts-ignore
      this.$analytics.onAttributePriceSavePressed(
        this.item.id,
        this.storeAddressId,
        this.adminUserId,
        this.currentAttributePrice,
        this.newPrice
      )
      return this.requestAttributePriceUpdate({
        attributeId: this.item.id,
        price: Number(this.newPrice),
        storeAddressId: this.storeAddressId!
      })
    }
    return null
  }

  async onPriceEditSave() {
    this.isSavingPriceUpdate = true
    await this.saveRequest()
    this.dismiss()
    this.showUndoBanner('stock.price_change.requested', () =>
      this.removeRequestPriceUpdate(this.value!.id)
    )
  }

  onPriceEditCancel() {
    this.dismiss()
  }

  showUndoBanner(key: string, onActionClick: (e: Event) => void) {
    this.$toast.show({
      message: this.$t(key).toString(),
      actionLabel: this.$t('common.undo').toString(),
      onActionClick
    })
  }

  async onPriceEditRevert() {
    this.isRemovingPriceUpdate = true
    await this.removeRequestPriceUpdate(this.value!.id)
    this.isRemovingPriceUpdate = false
    this.showUndoBanner('stock.price_change.removed', () =>
      this.restoreRequestPriceUpdate()
    )
  }

  dismiss() {
    this.isOpen = false
    this.newPrice = this.itemPrice
    this.isSavingPriceUpdate = false
  }

  onStartEdit() {
    this.isOpen = true
  }
}
