
import Placeholder from 'common/Placeholder.vue'
import ToggleBanner from '../ToggleBanner/ToggleBanner.vue'
import GlovoSearchField from 'common/ui/GlovoSearchField.vue'
import GlovoRadioGroup from 'common/ui/GlovoRadioGroup.vue'
import EditPrice from '../EditPrice/EditPrice.vue'
import NewEditPrice from '../EditPrice/NewEditPrice.vue'
import Stockout from '@/components/main/popups/Stockout.vue'
import EmptyCollection from '../EmptyCollection.vue'
import StockItemIndication from '../Stock/StockItemIndication.vue'
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import { Getter } from 'vuex-class'
import {
  Attribute,
  AttributeId,
  RequestPriceUpdate,
  RequestPriceUpdateId,
  StoreAddressId
} from '@/modules/store/data/resources'
import {
  GET_ATTRIBUTES_GETTER,
  GET_REQUESTED_PRICE_UPDATES_GETTER
} from '@/modules/store/data/getters'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'
import {
  SET_IS_ATTRIBUTE_SELECTED,
  SetIsAttributeSelectedPayload,
  REMOVE_ATTRIBUTE_STOCKOUT_NEW,
  RemoveAttributeStockoutNewPayload,
  UNDO_ATTRIBUTES_STOCKOUT,
  UndoAttributesStockoutPayload
} from '@/modules/stock/store/action-types'
import { GET_IS_SEARCHING_GETTER } from '@/modules/core/store/getters'
import { core } from '@/modules/core/store/namespace'
import { stockManagement } from '@/modules/stock/store/namespace'
import { StoreAction } from '@/modules/store/types/store'
import { NonEmptyArrayOf } from 'types/NonEmptyArrayOf'
import { AttributesDataState } from '@/modules/store/data/state'
import {
  DISPLAY_STOCK_ATTRIBUTES,
  DisplayType
} from '@/modules/store/services/display-types'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import { authentication } from '@/modules/authentication/store/namespace'
import { StoreAdmin } from '@/modules/store/data/resources'
import mParticle from '@/modules/mparticle'
import { BusinessEventNames } from 'models/BusinessEvents'
import {
  ProductStatusFilter,
  ProductStatusFilterTranslationKey,
  AttributeFilters
} from '@/modules/stock/types/ProductFilter'

@Component({
  name: 'Attributes',
  components: {
    ToggleBanner,
    GlovoSearchField,
    GlovoRadioGroup,
    Placeholder,
    NewEditPrice,
    EditPrice,
    Stockout,
    EmptyCollection,
    StockItemIndication
  }
})
export default class Attributes extends Vue {
  @Prop({ default: null }) storeAddressId!: StoreAddressId
  @Prop({ default: null }) activeStoreAddressId!: StoreAddressId

  loading = false
  selectedAttributeId = null
  selectedAttributeName = null
  selectedAttributeIds: AttributeId[] | null = null
  selectedAttributeNames: string[] | null = null
  isStockoutAttributesModalDisplayed = false
  selectedAttributes: Attribute[] | null = null
  filterBy: AttributeFilters = {
    searchTerm: '',
    status: ProductStatusFilter.ALL
  }

  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>

  @Getter display!: DisplayType // TODO to be migrated after all modules

  @Getter('isDesktop') isDesktop!: boolean

  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  storeAdmin?: StoreAdmin

  @Getter(GET_ATTRIBUTES_GETTER) attributesDataState!: AttributesDataState
  @Getter(GET_REQUESTED_PRICE_UPDATES_GETTER)
  requestedPriceUpdates!: Record<RequestPriceUpdateId, RequestPriceUpdate>

  @core.Getter(GET_IS_SEARCHING_GETTER)
  isSearching!: boolean

  @stockManagement.Action(REMOVE_ATTRIBUTE_STOCKOUT_NEW)
  removeAttributeStockoutNew!: StoreAction<RemoveAttributeStockoutNewPayload>

  @stockManagement.Action(SET_IS_ATTRIBUTE_SELECTED)
  setIsAttributeSelected!: StoreAction<SetIsAttributeSelectedPayload>

  @stockManagement.Action(UNDO_ATTRIBUTES_STOCKOUT)
  undoAttributesStockout!: StoreAction<UndoAttributesStockoutPayload>

  get allProductStatus() {
    return Object.entries(ProductStatusFilter).map(([, value]) => ({
      value,
      label: this.$t(ProductStatusFilterTranslationKey[value])
    }))
  }

  get attributes(): Attribute[] {
    if (!this.isCurrentlyBeingViewed) {
      return []
    }
    const attributeIds = this.attributesDataState.byStoreAddressId[
      this.storeAddressId
    ]
    if (!attributeIds || attributeIds.length === 0) {
      return []
    }
    const attributesFromDataState = attributeIds.map(
      id => this.attributesDataState.byId[id]
    )

    return attributesFromDataState.filter(attr => {
      if (this.isSearching) {
        const searchRegEx = new RegExp(this.searchTerm, 'i')
        const attributeMatchesRegex = Boolean(attr.name.match(searchRegEx))

        if (!attributeMatchesRegex) {
          return false
        }
      }

      switch (this.filterBy.status) {
        case ProductStatusFilter.ALL:
          return true
        case ProductStatusFilter.ENABLED:
          return attr.enabled
        case ProductStatusFilter.DISABLED:
          return !attr.enabled

        default:
          return false
      }
    })
  }

  get hasAttributes() {
    return this.attributes.length > 0
  }

  get isCurrentlyBeingViewed() {
    return (
      this.storeAddressId === this.activeStoreAddressId &&
      this.display.name === DISPLAY_STOCK_ATTRIBUTES.name
    )
  }

  get areMenuManagementChangesEnabled() {
    return this.toggles.FE_PARTNERS_MENU_MANAGEMENT_CHANGES_ENABLED
  }

  get isAttributeSearchEnabled() {
    return this.toggles.FE_PARTNERS_ATTRIBUTES_SEARCH_ENABLED
  }

  get storeAdminId() {
    return this.storeAdmin ? this.storeAdmin.id : 0
  }

  get totalSelectedAttributes() {
    const attributes: Attribute[] = []
    this.attributes.forEach(attribute => {
      if (attribute.selected && !attributes.includes(attribute))
        attributes.push(attribute)
    })
    return attributes
  }

  get areAllAttributesSelected(): boolean {
    return this.attributes.every(attribute => attribute.selected)
  }

  get areSomeAttributesSelected(): boolean {
    return (
      !this.areAllAttributesSelected &&
      this.attributes.some(attribute => attribute.selected)
    )
  }

  get displayEmptySearchState(): boolean {
    return this.isSearching && !this.hasAttributes
  }

  get searchTerm() {
    return this.filterBy.searchTerm
  }
  set searchTerm(value) {
    this.filterBy.searchTerm = value
  }

  closeStockoutAttributesModal() {
    this.selectedAttributeId = null
    this.selectedAttributeName = null
    this.selectedAttributeIds = null
    this.selectedAttributeNames = null
    this.isStockoutAttributesModalDisplayed = false
    if (this.selectedAttributes) {
      this.unselectAllAttributes(this.selectedAttributes)
    }
  }

  cancelStockoutAttributesModal() {
    mParticle.logEvent(
      BusinessEventNames.attributeDisableCancel,
      mParticle.EventType.UserContent,
      {
        storeAddressId: this.storeAddressId,
        storeAdminId: this.storeAdminId
      }
    )

    this.closeStockoutAttributesModal()
  }

  getSelectedAttributes(attributes: Attribute[]): Attribute[] {
    return this.isAtLeastOneAttributeSelected(attributes)
      ? attributes.filter(attr => attr.selected)
      : attributes
  }

  onCheckBoxChange(isSelected: boolean, id: AttributeId) {
    this.setIsAttributeSelected({
      id,
      isSelected
    })
  }

  getAttributeIds(attributes: Attribute[]) {
    return attributes.map(attribute => attribute.id)
  }

  async enableAttribute(attributeId: AttributeId) {
    await this.removeAttributeStockoutNew({
      storeAddressId: this.storeAddressId,
      attributeIds: [attributeId]
    })
  }

  async enableAttributes(attributes: Attribute[]) {
    this.loading = true
    const currentAttributes = this.getSelectedAttributes(attributes)
    const attributeIds = this.getAttributeIds(currentAttributes)
    await this.removeAttributeStockoutNew({
      storeAddressId: this.storeAddressId,
      attributeIds: attributeIds as NonEmptyArrayOf<AttributeId>
    })
    this.loading = false
    this.unselectAllAttributes(currentAttributes)
  }

  disableAttributes(attributes: Attribute[]) {
    this.selectedAttributes = this.getSelectedAttributes(attributes)
    if (this.selectedAttributes.length > 0) {
      this.selectedAttributeIds = this.selectedAttributes.map(a => a.id)
      this.selectedAttributeNames = this.selectedAttributes.map(a => a.name)
      this.isStockoutAttributesModalDisplayed = true
    }
    this.logMParticleDisableAttributes(this.selectedAttributes)
  }

  nothingSelected(attributes: Attribute[]) {
    return !attributes || attributes.filter(a => a.selected).length === 0
  }

  selectedAttributesFrom(attributes: Attribute[]) {
    return this.isAtLeastOneAttributeSelected(attributes)
      ? attributes.filter(a => a.selected).length
      : attributes
      ? attributes.length
      : 0
  }

  selectedAttributesLabel(attributes: Attribute[]) {
    return this.isAtLeastOneAttributeSelected(attributes)
      ? this.$t('attributes.seleted')
      : (this.$t('tab.attributes') as string).toLowerCase()
  }

  getEnableLabel(attributes: Attribute[]) {
    if (this.isAtLeastOneAttributeSelected(attributes)) {
      return this.$t('enable.label')
    }
    return this.$t('enable.all.label')
  }

  getDisableLabel(attributes: Attribute[]) {
    if (this.isAtLeastOneAttributeSelected(attributes)) {
      return this.$t('disable.label')
    }
    return this.$t('disable.all.label')
  }

  isAtLeastOneAttributeSelected(attributes: Attribute[]): boolean {
    return attributes && attributes.filter(a => a.selected).length > 0
  }

  unselectAllAttributes(attributes: Attribute[]) {
    attributes.forEach(({ id }) => {
      this.setIsAttributeSelected({
        id,
        isSelected: false
      })
    })
  }

  filterByStatus(status: ProductStatusFilter) {
    this.filterBy.status = status
  }

  logMParticleDisableAttributes(attributes: Attribute[]) {
    if (this.nothingSelected(attributes)) {
      mParticle.logEvent(
        BusinessEventNames.attributeDisableAll,
        mParticle.EventType.UserContent,
        {
          storeAddressId: this.storeAddressId,
          storeAdminId: this.storeAdminId
        }
      )
    } else {
      mParticle.logEvent(
        BusinessEventNames.attributeDisableSelected,
        mParticle.EventType.UserContent,
        {
          attributeIds: this.selectedAttributeIds,
          storeAddressId: this.storeAddressId,
          storeAdminId: this.storeAdminId
        }
      )
    }
  }

  async enableSelectedAttributes() {
    this.loading = true
    const currentAttributes = this.totalSelectedAttributes
    const attributeIds = this.getAttributeIds(currentAttributes)
    await this.removeAttributeStockoutNew({
      storeAddressId: this.storeAddressId,
      attributeIds: attributeIds as NonEmptyArrayOf<AttributeId>
    })
    this.loading = false
    this.showUndoBanner(attributeIds)
    this.unselectAllAttributes(currentAttributes)
  }

  showUndoBanner(attributeIds: AttributeId[]) {
    this.$toast.show({
      message: this.$tc('stockout.attributes.enabled', attributeIds.length),
      actionLabel: this.$t('common.undo').toString(),
      onActionClick: () => this.undoAttributesStockout(this.storeAddressId)
    })
  }

  disableSelectedAttributes() {
    this.selectedAttributes = this.totalSelectedAttributes
    if (this.selectedAttributes.length > 0) {
      this.selectedAttributeIds = this.selectedAttributes.map(a => a.id)
      this.selectedAttributeNames = this.selectedAttributes.map(a => a.name)
      this.isStockoutAttributesModalDisplayed = true
    }
    this.logMParticleDisableAttributes(this.selectedAttributes)
  }

  cancelSelectedAttributes() {
    this.unselectAllAttributes(this.totalSelectedAttributes)
  }

  formatAttributesAmountText(amount: number) {
    return `${amount} ${this.$tc('attributes.amount.text', amount)}`
  }

  toggleAllAttributes(isSelected: boolean) {
    this.attributes.forEach(({ id }) => {
      this.setIsAttributeSelected({
        id,
        isSelected
      })
    })
  }

  shouldDisplayDisabledAttributeForDesktop(attribute: Attribute) {
    return this.isDesktop && !attribute.enabled
  }

  shouldDisplayDisabledAttributeForNonDesktop(attribute: Attribute) {
    return !this.isDesktop && !attribute.enabled
  }
}
