
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import { Getter } from 'vuex-class'
import ProductItem from './ProductItem.vue'
import ProductsSectionItem from './ProductsSectionItem.vue'
import EditPrice from '../EditPrice/EditPrice.vue'
import mParticle from '@/modules/mparticle'
import { stockManagement } from '@/modules/stock/store/namespace'
import { authentication } from '@/modules/authentication/store/namespace'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import {
  GET_STORE_COLLECTION_GROUPS_GETTER,
  GET_STORE_COLLECTIONS_GETTER,
  GET_STORE_COLLECTION_SECTIONS_GETTER,
  GET_PRODUCTS_GETTER,
  GET_REQUESTED_PRICE_UPDATES_GETTER
} from '@/modules/store/data/getters'
import {
  REMOVE_PRODUCT_STOCKOUT_NEW,
  SET_IS_PRODUCT_SELECTED
} from '@/modules/stock/store/action-types'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import { BusinessEventNames } from 'models/BusinessEvents'
import type { NonEmptyArrayOf } from 'types/NonEmptyArrayOf'
import type { StoreAction } from '@/modules/store/types/store'
import type {
  Product,
  ProductId,
  StoreAddressId,
  StoreCollectionGroup,
  StoreCollectionGroupId,
  StoreCollection,
  StoreCollectionId,
  StoreCollectionSection,
  StoreCollectionSectionId,
  StoreCollectionSectionVM,
  RequestPriceUpdateId,
  RequestPriceUpdate
} from '@/modules/store/data/resources'
import type { FeatureToggle } from '@/modules/feature-toggles/store/state'
import type { StoreAdmin } from '@/modules/store/data/resources'
import type {
  RemoveProductStockoutNewPayload,
  SetIsProductSelectedPayload
} from '@/modules/stock/store/action-types'
import type { CollectionGroup } from './Products.vue'

type GroupId = number | null

@Component({
  name: 'ProductsListOld',
  components: {
    EditPrice,
    ProductItem,
    ProductsSectionItem
  }
})
export default class ProductsListOld extends Vue {
  @Prop({ default: null }) storeAddressId!: StoreAddressId
  @Prop({ default: null }) collectionGroups!: CollectionGroup[]
  @Prop() isVisible!: boolean
  @Prop() loading!: boolean

  groupId: GroupId = null

  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>

  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  storeAdmin?: StoreAdmin

  @Getter(GET_STORE_COLLECTION_GROUPS_GETTER) storeCollectionGroups!: Record<
    StoreCollectionGroupId,
    StoreCollectionGroup
  >

  @Getter(GET_STORE_COLLECTIONS_GETTER) storeCollections!: Record<
    StoreCollectionId,
    StoreCollection
  >

  @Getter(GET_STORE_COLLECTION_SECTIONS_GETTER)
  storeCollectionSections!: Record<
    StoreCollectionSectionId,
    StoreCollectionSection
  >

  @Getter(GET_PRODUCTS_GETTER) storeProducts!: Record<ProductId, Product>

  @stockManagement.Action(REMOVE_PRODUCT_STOCKOUT_NEW)
  removeProductStockoutNew!: StoreAction<RemoveProductStockoutNewPayload>

  @stockManagement.Action(SET_IS_PRODUCT_SELECTED)
  setIsProductSelected!: StoreAction<SetIsProductSelectedPayload>

  @Getter(GET_REQUESTED_PRICE_UPDATES_GETTER)
  requestedPriceUpdates!: Record<RequestPriceUpdateId, RequestPriceUpdate>

  mounted() {
    this.setInitialGroupId()
  }

  get areMenuManagementChangesEnabled() {
    return this.toggles.FE_PARTNERS_MENU_MANAGEMENT_CHANGES_ENABLED
  }

  get currentGroup() {
    if (this.isVisible) {
      return this.mapCollectionGroup(this.groupId)
    }

    return { collections: [] }
  }

  setInitialGroupId() {
    const firstCollectionGroupId = Object.keys(this.storeCollectionGroups)[0]
    this.groupId = firstCollectionGroupId
      ? parseInt(firstCollectionGroupId)
      : null
  }

  mapCollectionGroup(groupId: StoreCollectionGroupId | string | null) {
    if (groupId != null) {
      const collectionGroup = this.storeCollectionGroups[+groupId]
      const collections = collectionGroup.collections.map(this.mapCollections)
      return { ...collectionGroup, collections }
    }
    return { collections: [] }
  }

  mapCollections(collectionId: StoreCollectionId) {
    const collection = this.storeCollections[collectionId]

    const sections = collection.sections.map(this.mapSections)
    return { ...collection, sections }
  }

  mapSections(sectionId: StoreCollectionSectionId) {
    const section = this.storeCollectionSections[sectionId]

    const products = section.products.reduce(this.reduceProducts, [])
    return { ...section, products }
  }

  reduceProducts(products: Product[], productId: ProductId) {
    const product = this.storeProducts[productId]

    products.push(product)

    return products
  }

  getEnableLabel(section: StoreCollectionSectionVM) {
    if (section.products.some(p => p.selected)) {
      return this.$t('enable.label')
    }
    return this.$t('enable.all.label')
  }

  getDisableLabel(section: StoreCollectionSectionVM) {
    if (section.products.some(p => p.selected)) {
      return this.$t('disable.label')
    }

    return this.$t('disable.all.label')
  }

  getSelectedProductsFromSection(section: StoreCollectionSectionVM) {
    const selectedProducts = section.products.filter(p => p.selected)
    return selectedProducts.length > 0 ? selectedProducts : section.products
  }

  getProductIds(products: Product[]) {
    return products.map(product => product.id)
  }

  unselectAllProducts(products: Product[]) {
    products.forEach(({ id }) => {
      this.setIsProductSelected({
        id,
        isSelected: false
      })
    })
  }

  onCheckBoxChange(isSelected: boolean, id: ProductId) {
    this.setIsProductSelected({
      id,
      isSelected
    })
  }

  async enableProducts(section: StoreCollectionSectionVM) {
    if (!section || !section.products) {
      return
    }
    this.$emit('loading', true)
    const products = this.getSelectedProductsFromSection(section)
    const productIds = this.getProductIds(products)
    await this.removeProductStockoutNew({
      storeAddressId: this.storeAddressId,
      productIds: productIds as NonEmptyArrayOf<ProductId>
    })
    this.$emit('loading', false)
    this.unselectAllProducts(products)
  }

  disableProducts(section: StoreCollectionSectionVM) {
    if (!section || !section.products) {
      return
    }
    this.$emit(
      'set-selected-products',
      this.getSelectedProductsFromSection(section)
    )
    this.logMParticleDisableProducts(section)
  }

  logMParticleDisableProducts(section: StoreCollectionSectionVM) {
    const storeAdminId = this.storeAdmin ? this.storeAdmin.id : 0

    if (this.nothingSelected(section)) {
      mParticle.logEvent(
        BusinessEventNames.productDisableAll,
        mParticle.EventType.UserContent,
        {
          storeAddressId: this.storeAddressId,
          storeAdminId
        }
      )
    } else {
      mParticle.logEvent(
        BusinessEventNames.productDisableSelected,
        mParticle.EventType.UserContent,
        {
          productIds: this.getProductIds(
            this.getSelectedProductsFromSection(section)
          ),
          storeAddressId: this.storeAddressId,
          storeAdminId
        }
      )
    }
  }

  nothingSelected(section: StoreCollectionSectionVM) {
    return !section || section.products.filter(p => p.selected).length === 0
  }

  selectedProductFrom(section: StoreCollectionSectionVM) {
    return section && section.products.filter(p => p.selected).length > 0
      ? section.products.filter(p => p.selected).length
      : section.products
      ? section.products.length
      : 0
  }

  selectedProductsLabel(section: StoreCollectionSectionVM) {
    return section && section.products.filter(p => p.selected).length > 0
      ? this.$t('products.selected')
      : (this.$t('tab.products') as string).toLowerCase()
  }
}
