
import Component from 'vue-class-component'
import Vue from 'vue'
import {
  DISPLAY_STOCK_ATTRIBUTES,
  DISPLAY_STOCK_PRODUCTS,
  DisplayType
} from '@/modules/store/services/display-types'
import { Action, Getter } from 'vuex-class'
import { ToBeMigrated } from 'types/any'
import { Prop } from 'vue-property-decorator'
import { StoreAddressId } from '@/modules/store/data/resources'
import { StockStoreAddress } from './Stock.vue'
import Products from '../Products/Products.vue'
import Attributes from '../Attributes/Attributes.vue'
import Placeholder from 'common/Placeholder.vue'

@Component({
  name: 'StockTabs',
  components: {
    Products,
    Attributes,
    Placeholder
  }
})
export default class StockTabs extends Vue {
  @Prop() loading!: boolean
  @Prop() activeStoreAddressId!: StoreAddressId | null
  @Prop() storeAddress!: StockStoreAddress

  @Getter display!: DisplayType // TODO to be migrated after all modules
  @Getter isDesktop!: boolean // TODO to be migrated after all modules

  @Action('displayState') displayState: ToBeMigrated

  get activeTab() {
    return this.display.name
  }

  set activeTab(value) {
    if (value === 'PRODUCTS') {
      this.displayState(DISPLAY_STOCK_PRODUCTS)
    } else {
      this.displayState(DISPLAY_STOCK_ATTRIBUTES)
    }
  }
}
