
import Vue from 'vue'
import Component from 'vue-class-component'
import NewEditPrice from '../EditPrice/NewEditPrice.vue'
import GlovoBtn from '@/components/common/GlovoBtn.vue'
import StockItemIndication from '../Stock/StockItemIndication.vue'
import { Prop } from 'vue-property-decorator'
import { Getter } from 'vuex-class'
import { StoreAction } from '@/modules/store/types/store'
import { GET_REQUESTED_PRICE_UPDATES_GETTER } from '@/modules/store/data/getters'
import { stockManagement } from '@/modules/stock/store/namespace'
import {
  REMOVE_PRODUCT_STOCKOUT_NEW,
  RemoveProductStockoutNewPayload,
  SET_IS_PRODUCT_SELECTED,
  SetIsProductSelectedPayload
} from '@/modules/stock/store/action-types'
import type {
  Product,
  ProductId,
  RequestPriceUpdate,
  RequestPriceUpdateId,
  StoreAddressId
} from '@/modules/store/data/resources'

@Component({
  name: 'ProductItem',
  components: {
    NewEditPrice,
    GlovoBtn,
    StockItemIndication
  }
})
export default class ProductItem extends Vue {
  @Prop() storeAddressId!: StoreAddressId
  @Prop({ default: [] }) filteredProductIds!: ProductId[]
  @Prop() product!: Product

  @Getter(GET_REQUESTED_PRICE_UPDATES_GETTER)
  requestedPriceUpdates!: Record<RequestPriceUpdateId, RequestPriceUpdate>

  @Getter('isDesktop') isDesktop!: boolean

  @stockManagement.Action(REMOVE_PRODUCT_STOCKOUT_NEW)
  removeProductStockoutNew!: StoreAction<RemoveProductStockoutNewPayload>

  @stockManagement.Action(SET_IS_PRODUCT_SELECTED)
  setIsProductSelected!: StoreAction<SetIsProductSelectedPayload>

  get isVisible() {
    return this.filteredProductIds.some(
      filteredProductId => this.product.id === filteredProductId
    )
  }

  get requestedPriceUpdate() {
    if (!this.product.requestedPriceUpdateId) return null
    return this.requestedPriceUpdates[this.product.requestedPriceUpdateId]
  }

  get isProductDisabled() {
    return !this.product.enabled
  }

  get shouldDisplayDisabledProductForDesktop() {
    return this.isDesktop && this.isProductDisabled
  }

  get shouldDisplayDisabledProductForNonDesktop() {
    return !this.isDesktop && this.isProductDisabled
  }

  toggleCheckbox() {
    this.setIsProductSelected({
      id: this.product.id,
      isSelected: !this.product.selected
    })
  }

  async enableProduct() {
    await this.removeProductStockoutNew({
      storeAddressId: this.storeAddressId,
      productIds: [this.product.id]
    })
  }
}
