
import Vue from 'vue'
import Component from 'vue-class-component'
import { Action } from 'vuex-class'
import { DISPLAY_STOCK_PRODUCTS } from '@/modules/store/services/display-types'
import GBreadcrumb from '@/components/common/GBreadcrumb.vue'
import { scrollToActiveStoreAddressInStock } from '@/modules/stock/helpers/helpers'
import {
  CityDTO,
  StoreAddressDTO,
  StoreAdmin,
  StoreDTO
} from '@/modules/store/data/resources'
import {
  GET_CURRENT_STORE_ADMIN_GETTER,
  GET_SELECTED_STORE_ADDRESS_IDS_GETTER,
  GET_AVAILABLE_STORE_ADDRESS_IDS_GETTER
} from '@/modules/authentication'
import {
  GET_CONTENT,
  GET_ATTRIBUTES,
  GET_REQUESTED_PRICE_UPDATES
} from '../../store'
import { StoreAddressId } from '@/modules/store/data/resources/store-address'
import { authentication } from '@/modules/authentication/store/namespace'
import { core } from '@/modules/core/store/namespace'
import { stockManagement } from '@/modules/stock/store/namespace'
import {
  GetAttributesPayload,
  GetContentPayload,
  GetRequestedPriceUpdatesPayload
} from '@/modules/stock/store/action-types'
import {
  SET_IS_SEARCHING,
  SetIsSearchingPayload
} from '@/modules/core/store/action-types'
import { StoreAction } from '@/modules/store/types/store'
import { ToBeMigrated } from 'types/any'
import StockTabs from '@/modules/stock/components/Stock/StockTabs.vue'
import { Watch } from 'vue-property-decorator'

export interface StockStoreAddress {
  id: StoreAddressId
  store: string
  city: string
  address: string
  details: string
}

@Component({
  name: 'Stock',
  components: {
    StockTabs,
    GBreadcrumb
  }
})
export default class Stock extends Vue {
  loading = false
  activeStoreAddressId: StoreAddressId | null = null

  @authentication.Getter(GET_SELECTED_STORE_ADDRESS_IDS_GETTER)
  getSelectedStoreAddressIds!: StoreAddressId[]

  @authentication.Getter(GET_AVAILABLE_STORE_ADDRESS_IDS_GETTER)
  availableStoreAddresses!: StoreAddressId[]

  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  storeAdmin!: StoreAdmin

  @Action('displayState') displayState: ToBeMigrated

  @stockManagement.Action(GET_CONTENT)
  getContent!: StoreAction<GetContentPayload>

  @stockManagement.Action(GET_ATTRIBUTES)
  getAttributes!: StoreAction<GetAttributesPayload>

  @stockManagement.Action(GET_REQUESTED_PRICE_UPDATES)
  getRequestedPriceUpdates!: StoreAction<GetRequestedPriceUpdatesPayload>

  @core.Action(SET_IS_SEARCHING)
  setIsSearching!: StoreAction<SetIsSearchingPayload>

  get storeAddresses() {
    if (this.storeAdmin && this.storeAdmin.cities) {
      return this.getSelectedAddresses(this.storeAdmin.cities)
    } else {
      return []
    }
  }

  get hasOnlyOneStoreAddress() {
    return this.getSelectedStoreAddressIds.length === 1
  }

  mounted() {
    this.fetchSingleStoreAddressDetailsIfNecessary()
  }

  fetchSingleStoreAddressDetailsIfNecessary() {
    if (this.hasOnlyOneStoreAddress) {
      const id = this.getSelectedStoreAddressIds[0]
      this.activeStoreAddressId = id
      this.updateStoreAddressContent(id)
    }
  }

  @Watch('getSelectedStoreAddressIds')
  getSelectedStoreAddressIdsChange() {
    this.fetchSingleStoreAddressDetailsIfNecessary()
  }

  beforeMount() {
    this.displayState(DISPLAY_STOCK_PRODUCTS) // TODO can be refactored so page is responsive (only changes display on page load)
  }

  async updateStoreAddressContent(storeAddressId?: number) {
    if (storeAddressId) {
      this.loading = true
      await Promise.all([
        this.getContent(storeAddressId),
        this.getAttributes(storeAddressId)
      ])
      await this.getRequestedPriceUpdates(storeAddressId)
      this.loading = false
      scrollToActiveStoreAddressInStock(storeAddressId)
    }
  }

  // TODO improve the logic here. Use the vuex getters to get this data
  // https://glovoapp.atlassian.net/browse/PAR-2315
  getSelectedAddresses(cities: CityDTO[]): StockStoreAddress[] {
    const addresses: StockStoreAddress[] = []
    cities.forEach((city: CityDTO) => {
      city.stores.forEach((store: StoreDTO) => {
        store.storeAddresses.forEach(
          ({ id, address, details }: StoreAddressDTO) => {
            if (this.getSelectedStoreAddressIds.includes(id)) {
              const addressData = {
                id,
                store: store.name,
                city: city.code,
                address,
                details
              }
              addresses.push(addressData)
            }
          }
        )
      })
    })
    return addresses
  }

  onStoreAddressAccordionChange() {
    // this is necessary because the Product / Attribute component is not unmounted when el-collapse-item is closed
    this.setIsSearching(false)

    if (this.activeStoreAddressId) {
      this.updateStoreAddressContent(this.activeStoreAddressId)
    }
  }
}
