
import Vue from 'vue'
import Component from 'vue-class-component'
import GlovoBtn from 'common/GlovoBtn.vue'
import { Prop } from 'vue-property-decorator'
import { Attribute, Product } from '@/modules/store/data/resources'
import { dateFilter } from '@/modules/core/filters'

@Component({
  name: 'StockItemIndication',
  components: {
    GlovoBtn
  }
})
export default class StockItemIndication extends Vue {
  @Prop() stockItem!: Attribute | Product
  @Prop({ default: false }) isMobile!: boolean

  disabledText(stockItem: Attribute) {
    const stockout = stockItem.stockout
    if (!stockout) return

    return !stockout.expirationTime
      ? `${this.$t('stock.disabled.indefinitely')}`
      : this.stockDisabledUntilText(stockout.expirationTime)
  }

  stockDisabledUntilText(time: string | number): string {
    const timeString = this.convertTimeToString(time)
    const formattedTimeString = this.convertFirstLetterToLowercase(timeString)

    return `${this.$t('stock.disabled.until')} ${formattedTimeString}`
  }

  convertTimeToString(time: string | number): string {
    return dateFilter(time)
  }

  convertFirstLetterToLowercase(timeString: string): string {
    return timeString.charAt(0).toLowerCase() + timeString.slice(1)
  }

  enable() {
    this.$emit('enable', this.stockItem.id)
  }
}
