
import Vue from 'vue'
import Component from 'vue-class-component'
import GlovoBtn from 'common/GlovoBtn.vue'
import Icon from 'common/Icon.vue'
import { Prop } from 'vue-property-decorator'
import { Product } from '@/modules/store'

@Component({
  name: 'ToggleBanner',
  components: {
    GlovoBtn,
    Icon
  }
})
export default class ToggleBanner extends Vue {
  @Prop({ default: null }) selection!: Product[]
  @Prop({ default: null }) onEnable!: Function
  @Prop({ default: null }) onDisable!: Function
  @Prop({ default: null }) onCancel!: Function

  get selectionCount() {
    if (!this.selection) return 0
    return this.selection.length
  }
}
