import ZenScroll from 'zenscroll'
import {
  HALF_A_SECOND_IN_MILLIS,
  SCROLLING_OFFSET_TOP_IN_PIXELS
} from '@/modules/core/helpers'

class ScrollingService {
  constructor(private zenscroll: typeof ZenScroll) {
    this.zenscroll.setup(
      HALF_A_SECOND_IN_MILLIS,
      SCROLLING_OFFSET_TOP_IN_PIXELS
    )
  }

  scrollToElementId(elementId: string) {
    const element = document.getElementById(elementId)
    if (element) {
      this.zenscroll.to(element)
    }
  }
}

const scrollingService = new ScrollingService(ZenScroll)
export default scrollingService
