
import Vue from 'vue'
import Component from 'vue-class-component'
import Icon from 'common/Icon.vue'
import { Prop } from 'vue-property-decorator'

@Component({
  name: 'EmptyCollection',
  components: {
    Icon
  }
})
export default class EmptyCollection extends Vue {
  @Prop() collectionType!: string
  @Prop() emptyStateType!: string

  get iconName() {
    return `empty-${this.emptyStateType}`
  }

  get subtitleText() {
    return this.emptyStateType === 'search'
      ? this.$t(`no.${this.collectionType}.search`)
      : this.$t(`no.${this.collectionType}.collection`)
  }
}
