
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'

import type { Radio } from './GlovoRadioGroup'

@Component({
  name: 'GlovoRadioGroup'
})
export default class GlovoRadioGroup extends Vue {
  @Prop({ default: '' }) value!: string
  @Prop({ default: () => [] }) radioCollection!: Radio[]

  change(value: string) {
    this.$emit('change', value)
  }
}
