
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import { StoreAction } from '@/modules/store/types/store'
import { core } from '@/modules/core/store/namespace'
import { stockManagement } from '@/modules/stock/store/namespace'
import { GET_IS_SEARCHING_GETTER } from '@/modules/core/store/getters'
import {
  SET_IS_PRODUCT_SELECTED,
  SetIsProductSelectedPayload
} from '@/modules/stock/store/action-types'
import type {
  Product,
  ProductId,
  StoreCollectionSectionVM
} from '@/modules/store/data/resources'

@Component({
  name: 'ProductsSectionItem'
})
export default class ProductsSectionItem extends Vue {
  @Prop() section!: StoreCollectionSectionVM
  @Prop({ default: [] }) filteredProductIds!: ProductId[]
  @Prop({ default: null }) selectedProducts!: Product[] | null

  @Prop() isDividerVisible!: boolean

  @stockManagement.Action(SET_IS_PRODUCT_SELECTED)
  setIsProductSelected!: StoreAction<SetIsProductSelectedPayload>

  @core.Getter(GET_IS_SEARCHING_GETTER)
  isSearching!: boolean

  get filteredProducts() {
    return this.section.products.filter(product =>
      this.filteredProductIds.includes(product.id)
    )
  }

  get productAmountText() {
    const amount = this.filteredProducts.length

    return `${amount} ${this.$tc('order.products', amount)}`
  }

  get isVisible() {
    return this.hasFilteredProducts
  }

  get hasFilteredProducts() {
    return this.filteredProducts.length > 0
  }

  get isTitleVisible() {
    return !this.isSearching
  }

  get isSelected(): boolean {
    return this.filteredProducts.every(product =>
      this.selectedProducts?.find(selected => selected.id === product.id)
    )
  }

  get isIndeterminate(): boolean {
    return (
      !this.isSelected &&
      this.filteredProducts.some(product =>
        this.selectedProducts?.find(selected => selected.id === product.id)
      )
    )
  }

  toggleCheckbox() {
    this.filteredProducts.forEach(({ id }) => {
      this.setIsProductSelected({
        id,
        isSelected: !this.isSelected
      })
    })
  }
}
