
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import ProductItem from './ProductItem.vue'
import ProductsSectionItem from './ProductsSectionItem.vue'
import EmptyCollection from '../EmptyCollection.vue'
import { core } from '@/modules/core/store/namespace'
import { GET_IS_SEARCHING_GETTER } from '@/modules/core/store/getters'
import {
  ProductFilters,
  PRODUCTS_FILTERS_INITIAL_STATE
} from '@/modules/stock/types/ProductFilter'
import type {
  Product,
  ProductId,
  StoreAddressId,
  StoreCollectionSectionId,
  StoreCollectionId,
  StoreCollectionGroupId
} from '@/modules/store/data/resources'
import type { CollectionGroup } from './Products.vue'
import { MenuItemType } from '@/components/common/ui/collection-list-types'

@Component({
  name: 'ProductsList',
  components: {
    EmptyCollection,
    ProductItem,
    ProductsSectionItem
  }
})
export default class ProductsList extends Vue {
  @Prop({ default: null }) storeAddressId!: StoreAddressId
  @Prop({ default: null }) collectionGroups!: CollectionGroup[]
  @Prop({ default: null }) selectedProducts!: Product[] | null
  @Prop({ default: [] }) filteredProductIds!: ProductId[]
  @Prop({ default: PRODUCTS_FILTERS_INITIAL_STATE }) filterBy!: ProductFilters

  @core.Getter(GET_IS_SEARCHING_GETTER)
  isSearching!: boolean

  get filteredProductsQuantity() {
    return this.filteredProductIds.length
  }

  get hasFilteredProducts() {
    return this.filteredProductsQuantity > 0
  }

  get isSearchTitleVisible() {
    return this.isSearching && this.hasFilteredProducts
  }

  get emptyStateType() {
    return this.isSearching ? 'search' : 'collections'
  }

  get emptyStateDataQA() {
    return this.isSearching ? 'empty-search' : 'empty-collection'
  }

  isSectionVisible(sectionId: StoreCollectionSectionId) {
    return (
      this.filterBy.group.type !== MenuItemType.SECTION ||
      this.filterBy.group.id === sectionId
    )
  }

  isCollectionVisible(collectionId: StoreCollectionId) {
    return (
      this.filterBy.group.type !== MenuItemType.COLLECTION ||
      this.filterBy.group.id === collectionId
    )
  }

  isCollectionGroupVisible(collectionGroupId: StoreCollectionGroupId) {
    return (
      this.filterBy.group.type !== MenuItemType.COLLECTION_GROUP ||
      this.filterBy.group.id === collectionGroupId
    )
  }

  lastSeparator(index: number, length: number) {
    return index === length - 1
  }
}
